* {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  display: flex;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  font-size: 12px;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  grid-gap: 46px 48px;
  color: #060b35;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-inline: 16px;
  font-size: 10px;
  font-weight: 400;
  transition: all .3s;
}

.card__top {
  margin-top: 32px;
  margin-bottom: 16px;
}

.card__img {
  width: 160px;
  min-height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  width: 16px;
  height: 16px;
}

